  padding: 0;
  width: 100%;
  height: 100%;
  font-size: 16px;
  line-height: 1.7;
  backface-visibility: hidden;
  /* overflow-x: hidden; */ }

body {
  position: relative;
  overflow-x: hidden;
  height: 100%;
  min-width: 320px;
  font-family: "RobotoRegular", sans-serif;
  font-size: 16px;
  line-height: 1.7;
  color: #000000;
  opacity: 1; }
  body input,
  body textarea {
    outline: none;
    border: #666666 1px solid; }
    body input:required:valid,
    body textarea:required:valid {
      border-color: #008000; }
    body input:focus:required:invalid,
    body textarea:focus:required:invalid {
      border-color: #ff0000; }

.img-responsive {
  display: block;
  height: auto;
  max-width: 100%; }
  .img-responsive.img-width {
    width: 100%; }

.container {
  border: 1px solid #ff0000;
  min-height: 230px;
  background-color: #008000; }

.home__header {
  padding-top: 70px;
  min-height: 100%;
  color: #ffffff;
  background-color: #252525; }

.top__line {
  position: absolute;
  top: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  width: 100%;
  height: 70px;
  background-color: #252525; }

.top__line--logo {
  display: flex;
  flex: 0 0 200px;
  align-items: center;
  font-weight: 500;
  font-size: 13px;
  letter-spacing: 0.5em;
  text-decoration: none;
  color: #ffffff;
  opacity: 1;
  transition: opacity 0.5s ease; }
  .top__line--logo:hover {
    opacity: 0.9; }

.top__line--decor {
  flex: 1 0 0;
  align-items: center;
  text-align: center; }
  .top__line--decor::before {
    content: "";
    position: absolute;
    top: 50%;
    display: inline-block;
    width: 50px;
    height: 2px;
    background-color: #ffffff;
    opacity: 0.2; }

.logo__img {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70px;
  height: 70px; }

.menu__button--container {
  position: relative;
  display: flex;
  flex: 0 0 200px;
  align-items: center;
  justify-content: center;
  height: 70px;
  text-align: right; }

.menu__button {
  position: absolute;
  right: 0;
  top: 0;
  width: 70px;
  height: 70px;
  color: #ffffff;
  background-image: url("../img/bars-solid.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-color: #303030;
  transition: all 0.5 ease; }
  .menu__button:hover {
    background-color: #f57500; }

.top__line--contacts {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 500;
  font-size: 10px;
  letter-spacing: 0.4em;
  text-transform: uppercase;
  color: #ffffff;
  opacity: 0.4; }

.top__line--separator {
  margin-right: 20px;
  margin-left: 20px;
  width: 2px;
  height: 30px;
  background-color: #ffffff;
  opacity: 0.2; }

.top__line--numbers,
.top__line--adres {
  display: inline-block;
  margin-left: 5px;
  font-family: "RobotoRegular", serif;
  text-decoration: none;
  color: #f57500;
  opacity: 0.75; }

.home__header--content {
  height: 100%; }

.home__aside {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 70px;
  width: 70px;
  height: 100%;
  background-color: #303030; }

.home__aside-decor {
  flex: 1 0 0;
  width: 100%;
  background-image: url("../img/decoration.svg");
  background-position: center;
  background-repeat: no-repeat; }

.home__aside-soc {
  flex: 1 0 0; }

.home__aside-shareicon {
  display: flex;
  flex: 0 0 70px;
  align-items: center;
  justify-content: center;
  width: 70px;
  height: 70px;
  background: #f57500;
  background-image: url("../img/share-solid.svg");
  background-position: center;
  background-size: 12px;
  background-repeat: no-repeat; }

.soc__item {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -1px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid transparent;
  width: 40px;
  height: 40px;
  transition: background-color 0.25 ease-in-out; }
  .soc__item img {
    width: 15px;
    height: auto;
    max-height: 15px;
    opacity: 0.3;
    transition: opacity 0.25 ease-in-out; }
  .soc__item:hover {
    background-color: #f57500; }
    .soc__item:hover img {
      opacity: 1; }
  .soc__item:last-child {
    border: 1px solid rgba(255, 255, 255, 0.5); }

.slider__container {
  position: absolute;
  bottom: 0;
  overflow: hidden;
  padding-left: 70px;
  width: 100%;
  height: 100%; }

.slider__bottom {
  position: absolute;
  left: 70px;
  bottom: 0;
  z-index: 5;
  display: flex;
  align-items: center;
  width: calc(75% - 70px);
  height: 70px; }

.slider__share {
  flex: 0 1 auto; }

.slider__share__items {
  position: relative;
  bottom: -1px;
  display: flex; }

.slider__share-unit {
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  width: 70px;
  height: 70px;
  background-color: #303030; }
  .slider__share-unit:last-child {
    border-right: none;
    border-bottom: none; }
    .slider__share-unit:last-child::after {
      content: "";
      position: absolute;
      right: 0;
      width: 3px;
      height: 70px;
      background-color: #f57500; }

.slider__img {
  position: relative;
  overflow: hidden;
  width: 75%;
  height: 100%; }
  .slider__img::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0.45; }

.slider__img--slide {
  overflow: hidden;
  height: 100%; }

.slider__img--bg {
  height: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat; }

.slider__text {
  position: absolute;
  z-index: 3;
  width: 100%;
  height: 100%; }

.swiper__text--slide {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.slider-text__content {
  position: relative;
  padding-right: 22%;
  text-align: right; }

.slider-text__heading {
  float: right;
  max-width: 780px;
  font-family: "OswaldBold", sans-serif;
  font-size: 60px;
  line-height: 120%;
  text-transform: uppercase; }

.slider-text__heading span {
  white-space: nowrap;
  color: #f57500; }

.slider-text__desc {
  clear: both;
  float: right;
  margin-top: 15px;
  margin-bottom: 20px;
  max-width: 500px;
  font-weight: 500;
  font-size: 13px; }

.button {
  position: relative;
  display: inline-block;
  border: none;
  padding: 14px 26px;
  font-weight: 800;
  font-size: 10px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #f57500;
  cursor: pointer; }

.button::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 100%;
  background-color: #303030;
  transition: width 0.5s ease-out; }

.button:hover::before {
  width: 100%; }

.button_slider {
  clear: both;
  float: right; }

.button__text {
  position: relative;
  z-index: 2; }

.slider__pagination {
  position: relative;
  display: flex;
  flex: 0 0 140px;
  align-items: center;
  justify-content: center;
  height: 70px; }

.slider__pagination .swiper-pagination-bullet {
  margin: 0 8px;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  background-color: #ffffff;
  opacity: 1; }

.slider__pagination .swiper-pagination-bullet-active {
  position: relative;
  background-color: #f57500; }

.slider__pagination .swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  left: -4px;
  top: -4px;
  border: 1px solid #f57500;
  border-radius: 100%;
  width: 14px;
  height: 14px; }

.slider__scrollbar {
  flex: 1 0 0; }

.slider__scrollbar .swiper-scrollbar {
  height: 1px;
  background-color: rgba(255, 255, 255, 0.2); }

.slider__scrollbar .swiper-scrollbar-drag {
  top: -1px;
  margin-top: -1px;
  height: 3px;
  background-color: #f57500; }

.slider__navigation {
  position: relative;
  flex: 0 0 140px;
  height: 70px; }

.slider__navigation .swiper-button-disabled {
  color: #8a8a8a;
  background-color: #8a8a8a;
  opacity: 1; }

.slider__navigation .swiper-button-next {
  right: 0;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  color: #f57500;
  background-color: #3c3c3c; }

.slider__navigation .swiper-button-prev {
  left: 0;
  border: 2px solid #f57500;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  color: #f57500;
  background-color: #3c3c3c; }

.swiper-button-prev::after,
.swiper-button-next::after {
  font-size: 10px; }

.sn__container {
  position: relative;
  margin: auto;
  height: 35px;
  max-width: 70px; }

.slider__gear {
  position: absolute;
  right: calc(24% - 235px);
  top: 50%;
  z-index: 2;
  margin-top: -270px;
  width: 470px;
  height: 470px;
  background-image: url(../img/gear.svg);
  background-position: center;
  background-size: cover; }

.slider__pagination-count {
  position: absolute;
  right: 20px;
  top: 30px;
  z-index: 100;
  width: 120px;
  height: 80px;
  font-weight: 500;
  font-size: 24px; }

.slider__pagination-count .current {
  display: inline-block;
  min-width: 42px;
  font-size: 40px;
  color: #f57500; }

.slider__pagin-current {
  position: absolute;
  right: 40px;
  bottom: 10px;
  font-size: 100px;
  -webkit-text-stroke: 1px #7f7f7f;
  color: #252525; }

.slider-pagination-current__dot,
.slider__pagin-dot {
  -webkit-text-stroke: 1px transparent;
  color: #f57500; }

/**************************  \
  Basic Modal Styles
\  **************************/
.modal {
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif; }

#modal-3 {
  display: none; }

#modal-3.is-open {
  display: block; }

.modal__overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.6); }

.modal__container {
  overflow-y: auto;
  border-radius: 4px;
  padding: 30px;
  max-width: 500px;
  max-height: 100vh;
  box-sizing: border-box;
  background-color: #ffffff; }

.modal__header {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.modal__title {
  margin-top: 0;
  margin-bottom: 0;
  box-sizing: border-box;
  font-weight: 600;
  font-size: 1.25rem;
  line-height: 1.25;
  color: #00449e; }

.modal__close {
  border: 0;
  background: transparent; }

.modal__header .modal__close::before {
  content: "\2715"; }

.modal__content {
  margin-top: 2rem;
  margin-bottom: 2rem;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.8); }

.modal__btn {
  appearance: button;
  overflow: visible;
  margin: 0;
  border-style: none;
  border-width: 0;
  border-radius: 0.25rem;
  padding-top: 0.5rem;
  padding-right: 1rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  -moz-osx-font-smoothing: grayscale;
  font-size: 0.875rem;
  line-height: 1.15;
  text-transform: none;
  color: rgba(0, 0, 0, 0.8);
  background-color: #e6e6e6;
  backface-visibility: hidden;
  transform: translateZ(0);
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
  will-change: transform;
  cursor: pointer; }

.modal__btn:focus,
.modal__btn:hover {
  transform: scale(1.05); }

.modal__btn-primary {
  color: #ffffff;
  background-color: #00449e; }

/**************************  \
  Demo Animation Style
\  **************************/
@keyframes mmfadeIn {
  from {
    opacity: 0;
    transform: translateY(15%); }
  to {
    opacity: 1;
    transform: translateY(0); } }

@keyframes mmfadeOut {
  from {
    opacity: 1;
    transform: translateY(0); }
  to {
    opacity: 0;
    transform: translateY(-15%); } }

.micromodal-slide {
  display: none; }

.micromodal-slide.is-open {
  display: block; }

.micromodal-slide[aria-hidden="false"] .modal__overlay {
  animation: mmfadeIn 0.8s cubic-bezier(0, 0, 0.2, 1); }

.micromodal-slide[aria-hidden="false"] .modal__container {
  animation: mmfadeIn 0.8s cubic-bezier(0, 0, 0.2, 1); }

.micromodal-slide[aria-hidden="true"] .modal__overlay {
  animation: mmfadeOut 0.8s cubic-bezier(0, 0, 0.2, 1); }

.micromodal-slide[aria-hidden="true"] .modal__container {
  animation: mmfadeOut 0.8s cubic-bezier(0, 0, 0.2, 1); }

.micromodal-slide .modal__container,
.micromodal-slide .modal__overlay {
  will-change: transform; }

.block {
  padding: 30px 0;
  height: 1000px;
  background-color: #303030; }

.modal {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 1000000000;
